export enum InputType {
  Birth,
  Height,
  Weight,
  BodyFat,
}

type InputTypeMap = {
  [key in InputType]: {
    text: string;
    type: InputType;
    placeholder: string;
  };
};

export const INPUT_TYPE: InputTypeMap = {
  [InputType.Birth]: { text: '', type: InputType.Birth, placeholder: '2001/01/01' },
  [InputType.Height]: { text: '', type: InputType.Height, placeholder: '175' },
  [InputType.Weight]: { text: '', type: InputType.Weight, placeholder: '70' },
  [InputType.BodyFat]: { text: '', type: InputType.BodyFat, placeholder: '20' },
};
